// ============================================
// RISIVO CRM - Agency Twilio Configuration API
// Manage master Twilio account settings
// ============================================

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin as supabase } from '@/lib/supabase/server'
import { twilioService } from '@/lib/telephony/twilio-service'

// ==========================================
// GET - Retrieve agency Twilio configuration
// ==========================================

export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const agencyId = searchParams.get('agencyId') // Optional - defaults to system config if null

    // Get configuration
    const query = supabase
      .from('AgencyTwilioConfig')
      .select('*')

    if (agencyId) {
      query.eq('agencyId', agencyId)
    } else {
      query.is('agencyId', null) // Get system/default config
    }

    const { data, error } = await query.single()

    if (error && error.code !== 'PGRST116') { // PGRST116 = no rows found
      throw error
    }

    // Mask sensitive fields for response
    if (data) {
      const maskedData = {
        ...data,
        authToken: data.authToken ? '••••••••' : null,
        apiKeySecret: data.apiKeySecret ? '••••••••' : null,
        sendgridApiKey: data.sendgridApiKey ? '••••••••' : null,
        webhookSecretKey: data.webhookSecretKey ? '••••••••' : null,
      }
      return NextResponse.json({ success: true, data: maskedData })
    }

    return NextResponse.json({ success: true, data: null })
  } catch (error) {
    console.error('Error fetching Twilio config:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to fetch configuration' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST - Create new agency Twilio configuration
// ==========================================

export async function POST(request: NextRequest) {
  try {
    const body = await request.json()

    // Validate required fields
    if (!body.accountSid || !body.authToken) {
      return NextResponse.json(
        { success: false, error: 'Account SID and Auth Token are required' },
        { status: 400 }
      )
    }

    // Validate credentials with Twilio
    const validation = await twilioService.validateMasterCredentials(
      body.accountSid,
      body.authToken
    )

    if (!validation.valid) {
      return NextResponse.json(
        { success: false, error: `Invalid Twilio credentials: ${validation.error}` },
        { status: 400 }
      )
    }

    // Check if config already exists
    const { data: existing } = await supabase
      .from('AgencyTwilioConfig')
      .select('id')
      .is('agencyId', body.agencyId || null)
      .single()

    if (existing) {
      return NextResponse.json(
        { success: false, error: 'Configuration already exists. Use PUT to update.' },
        { status: 409 }
      )
    }

    // Create configuration
    const configData: Record<string, unknown> = {
      platformId: body.agencyId || null, // Using agencyId from request as platformId
      accountSid: body.accountSid,
      authToken: body.authToken,
      apiKeySid: body.apiKeySid || null,
      apiKeySecret: body.apiKeySecret || null,
      sendgridApiKey: body.sendgridApiKey || null,
      usageMarkupPercent: body.usageMarkupPercent ?? 5,
      enableSubaccountBilling: body.enableSubaccountBilling ?? true,
      billingCurrency: body.billingCurrency || 'USD',
      autoProvisionEnabled: body.autoProvisionEnabled ?? true,
      autoProvisionPhoneNumber: body.autoProvisionPhoneNumber ?? false,
      defaultPhoneNumberType: body.defaultPhoneNumberType || 'local',
      defaultPhoneNumberCountry: body.defaultPhoneNumberCountry || 'US',
      defaultPhoneNumberAreaCode: body.defaultPhoneNumberAreaCode || null,
      webhookBaseUrl: body.webhookBaseUrl || null,
      smsEnabled: body.smsEnabled ?? true,
      voiceEnabled: body.voiceEnabled ?? true,
      faxEnabled: body.faxEnabled ?? false,
      mmsEnabled: body.mmsEnabled ?? true,
      brandRegistrationEnabled: body.brandRegistrationEnabled ?? true,
      defaultBrandType: body.defaultBrandType || 'STANDARD',
      require10DLCForSms: body.require10DLCForSms ?? true,
      smsBounceWarningThreshold: body.smsBounceWarningThreshold ?? 5,
      smsBounceSuspendThreshold: body.smsBounceSuspendThreshold ?? 10,
      smsSpamReportThreshold: body.smsSpamReportThreshold ?? 0.1,
      callAbandonRateWarning: body.callAbandonRateWarning ?? 3,
      dailySmsLimitDefault: body.dailySmsLimitDefault ?? 1000,
      dailyCallLimitDefault: body.dailyCallLimitDefault ?? 500,
      smsPerSecondLimit: body.smsPerSecondLimit ?? 10,
      callsPerSecondLimit: body.callsPerSecondLimit ?? 5,
      isActive: true,
      lastValidatedAt: new Date().toISOString(),
    }

    const { data, error } = await supabase
      .from('AgencyTwilioConfig')
      .insert(configData)
      .select()
      .single()

    if (error) throw error

    // Mask sensitive fields
    const maskedData = {
      ...data,
      authToken: '••••••••',
      apiKeySecret: data.apiKeySecret ? '••••••••' : null,
      sendgridApiKey: data.sendgridApiKey ? '••••••••' : null,
      webhookSecretKey: data.webhookSecretKey ? '••••••••' : null,
    }

    return NextResponse.json({ success: true, data: maskedData }, { status: 201 })
  } catch (error) {
    console.error('Error creating Twilio config:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to create configuration' },
      { status: 500 }
    )
  }
}

// ==========================================
// PUT - Update agency Twilio configuration
// ==========================================

export async function PUT(request: NextRequest) {
  try {
    const body = await request.json()

    if (!body.id) {
      return NextResponse.json(
        { success: false, error: 'Configuration ID is required' },
        { status: 400 }
      )
    }

    // Get existing config
    const { data: existing, error: fetchError } = await supabase
      .from('AgencyTwilioConfig')
      .select('*')
      .eq('id', body.id)
      .single()

    if (fetchError || !existing) {
      return NextResponse.json(
        { success: false, error: 'Configuration not found' },
        { status: 404 }
      )
    }

    // If credentials are being updated, validate them
    const newAccountSid = body.accountSid || existing.accountSid
    const newAuthToken = body.authToken && body.authToken !== '••••••••' 
      ? body.authToken 
      : existing.authToken

    if (body.accountSid || (body.authToken && body.authToken !== '••••••••')) {
      const validation = await twilioService.validateMasterCredentials(
        newAccountSid,
        newAuthToken
      )

      if (!validation.valid) {
        return NextResponse.json(
          { success: false, error: `Invalid Twilio credentials: ${validation.error}` },
          { status: 400 }
        )
      }
    }

    // Build update object
    const updateData: Record<string, unknown> = {
      updatedAt: new Date().toISOString(),
    }

    // Only update fields that were provided
    const fields = [
      'accountSid', 'apiKeySid', 'sendgridApiKey', 'usageMarkupPercent',
      'enableSubaccountBilling', 'billingCurrency', 'autoProvisionEnabled',
      'autoProvisionPhoneNumber', 'defaultPhoneNumberType', 'defaultPhoneNumberCountry',
      'defaultPhoneNumberAreaCode', 'webhookBaseUrl', 'webhookSecretKey',
      'smsEnabled', 'voiceEnabled', 'faxEnabled', 'mmsEnabled',
      'brandRegistrationEnabled', 'defaultBrandType', 'require10DLCForSms',
      'smsBounceWarningThreshold', 'smsBounceSuspendThreshold', 'smsSpamReportThreshold',
      'callAbandonRateWarning', 'dailySmsLimitDefault', 'dailyCallLimitDefault',
      'smsPerSecondLimit', 'callsPerSecondLimit', 'isActive'
    ]

    fields.forEach(field => {
      if (body[field] !== undefined) {
        updateData[field] = body[field]
      }
    })

    // Handle sensitive fields separately (don't update if masked value sent)
    if (body.authToken && body.authToken !== '••••••••') {
      updateData.authToken = body.authToken
      updateData.lastValidatedAt = new Date().toISOString()
    }
    if (body.apiKeySecret && body.apiKeySecret !== '••••••••') {
      updateData.apiKeySecret = body.apiKeySecret
    }
    if (body.sendgridApiKey && body.sendgridApiKey !== '••••••••') {
      updateData.sendgridApiKey = body.sendgridApiKey
    }

    const { data, error } = await supabase
      .from('AgencyTwilioConfig')
      .update(updateData)
      .eq('id', body.id)
      .select()
      .single()

    if (error) throw error

    // Mask sensitive fields
    const maskedData = {
      ...data,
      authToken: '••••••••',
      apiKeySecret: data.apiKeySecret ? '••••••••' : null,
      sendgridApiKey: data.sendgridApiKey ? '••••••••' : null,
      webhookSecretKey: data.webhookSecretKey ? '••••••••' : null,
    }

    return NextResponse.json({ success: true, data: maskedData })
  } catch (error) {
    console.error('Error updating Twilio config:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to update configuration' },
      { status: 500 }
    )
  }
}

// ==========================================
// DELETE - Remove agency Twilio configuration
// ==========================================

export async function DELETE(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const id = searchParams.get('id')

    if (!id) {
      return NextResponse.json(
        { success: false, error: 'Configuration ID is required' },
        { status: 400 }
      )
    }

    // Check for dependent subaccounts
    const { data: subaccounts } = await supabase
      .from('TwilioSubaccount')
      .select('id')
      .eq('agencyConfigId', id)
      .limit(1)

    if (subaccounts && subaccounts.length > 0) {
      return NextResponse.json(
        { success: false, error: 'Cannot delete configuration with active subaccounts' },
        { status: 409 }
      )
    }

    const { error } = await supabase
      .from('AgencyTwilioConfig')
      .delete()
      .eq('id', id)

    if (error) throw error

    return NextResponse.json({ success: true })
  } catch (error) {
    console.error('Error deleting Twilio config:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to delete configuration' },
      { status: 500 }
    )
  }
}
