// ==========================================
// TWILIO API CLIENT
// ==========================================

interface TwilioCredentials {
  accountSid: string
  authToken: string
}

class TwilioApiClient {
  private accountSid: string
  private authToken: string
  private baseUrl = 'https://api.twilio.com/2010-04-01'
  
  constructor(credentials: TwilioCredentials) {
    this.accountSid = credentials.accountSid
    this.authToken = credentials.authToken
  }
  
  private getAuthHeader(): string {
    const credentials = Buffer.from(`${this.accountSid}:${this.authToken}`).toString('base64')
    return `Basic ${credentials}`
  }
  
  private async request<T>(
    method: string,
    path: string,
    body?: Record<string, unknown>,
    useFormData = true
  ): Promise<T> {
    const url = `${this.baseUrl}${path}`
    
    const headers: Record<string, string> = {
      'Authorization': this.getAuthHeader(),
    }
    
    let requestBody: string | undefined
    
    if (body && useFormData) {
      headers['Content-Type'] = 'application/x-www-form-urlencoded'
      requestBody = new URLSearchParams(
        Object.entries(body).reduce((acc, [key, value]) => {
          if (value !== undefined && value !== null) {
            acc[key] = String(value)
          }
          return acc
        }, {} as Record<string, string>)
      ).toString()
    } else if (body) {
      headers['Content-Type'] = 'application/json'
      requestBody = JSON.stringify(body)
    }
    
    const response = await fetch(url, {
      method,
      headers,
      body: requestBody,
    })
    
    if (!response.ok) {
      const error = await response.json()
      throw new TwilioApiError(
        error.message || 'Twilio API error',
        error.code,
        response.status
      )
    }
    
    return response.json()
  }
  
  // ==========================================
  // ACCOUNT MANAGEMENT
  // ==========================================
  
  /**
   * Create a Twilio subaccount
   */
  async createSubaccount(friendlyName: string): Promise<TwilioSubaccountResponse> {
    return this.request<TwilioSubaccountResponse>(
      'POST',
      `/Accounts.json`,
      { FriendlyName: friendlyName }
    )
  }
  
  /**
   * Get subaccount details
   */
  async getSubaccount(accountSid: string): Promise<TwilioSubaccountResponse> {
    return this.request<TwilioSubaccountResponse>(
      'GET',
      `/Accounts/${accountSid}.json`
    )
  }
  
  /**
   * Update subaccount status (suspend/activate/close)
   */
  async updateSubaccountStatus(
    accountSid: string, 
    status: 'active' | 'suspended' | 'closed'
  ): Promise<TwilioSubaccountResponse> {
    return this.request<TwilioSubaccountResponse>(
      'POST',
      `/Accounts/${accountSid}.json`,
      { Status: status }
    )
  }
  
  // ==========================================
  // PHONE NUMBER MANAGEMENT
  // ==========================================
  
  /**
   * Search available phone numbers
   */
  async searchAvailableNumbers(
    country: string,
    type: PhoneNumberType,
    options: {
      areaCode?: string
      contains?: string
      locality?: string
      region?: string
      limit?: number
    } = {}
  ): Promise<TwilioAvailableNumbersResponse> {
    const params = new URLSearchParams()
    
    if (options.areaCode) params.append('AreaCode', options.areaCode)
    if (options.contains) params.append('Contains', options.contains)
    if (options.locality) params.append('InLocality', options.locality)
    if (options.region) params.append('InRegion', options.region)
    params.append('PageSize', String(options.limit || 20))
    
    const endpoint = type === 'toll_free' ? 'TollFree' : 'Local'
    
    return this.request<TwilioAvailableNumbersResponse>(
      'GET',
      `/Accounts/${this.accountSid}/AvailablePhoneNumbers/${country}/${endpoint}.json?${params.toString()}`,
      undefined,
      false
    )
  }
  
  /**
   * Purchase a phone number
   */
  async purchaseNumber(
    phoneNumber: string,
    options: {
      friendlyName?: string
      voiceUrl?: string
      voiceMethod?: string
      smsUrl?: string
      smsMethod?: string
      statusCallbackUrl?: string
    } = {}
  ): Promise<TwilioIncomingPhoneNumberResponse> {
    return this.request<TwilioIncomingPhoneNumberResponse>(
      'POST',
      `/Accounts/${this.accountSid}/IncomingPhoneNumbers.json`,
      {
        PhoneNumber: phoneNumber,
        FriendlyName: options.friendlyName,
        VoiceUrl: options.voiceUrl,
        VoiceMethod: options.voiceMethod || 'POST',
        SmsUrl: options.smsUrl,
        SmsMethod: options.smsMethod || 'POST',
        StatusCallback: options.statusCallbackUrl,
      }
    )
  }
  
  /**
   * Update phone number configuration
   */
  async updateNumber(
    phoneNumberSid: string,
    options: {
      friendlyName?: string
      voiceUrl?: string
      voiceMethod?: string
      smsUrl?: string
      smsMethod?: string
      statusCallbackUrl?: string
      voiceApplicationSid?: string
      smsApplicationSid?: string
    }
  ): Promise<TwilioIncomingPhoneNumberResponse> {
    return this.request<TwilioIncomingPhoneNumberResponse>(
      'POST',
      `/Accounts/${this.accountSid}/IncomingPhoneNumbers/${phoneNumberSid}.json`,
      {
        FriendlyName: options.friendlyName,
        VoiceUrl: options.voiceUrl,
        VoiceMethod: options.voiceMethod,
        SmsUrl: options.smsUrl,
        SmsMethod: options.smsMethod,
        StatusCallback: options.statusCallbackUrl,
        VoiceApplicationSid: options.voiceApplicationSid,
        SmsApplicationSid: options.smsApplicationSid,
      }
    )
  }
  
  /**
   * Release a phone number
   */
  async releaseNumber(phoneNumberSid: string): Promise<void> {
    await this.request<void>(
      'DELETE',
      `/Accounts/${this.accountSid}/IncomingPhoneNumbers/${phoneNumberSid}.json`
    )
  }
  
  /**
   * List phone numbers for account
   */
  async listNumbers(options: {
    pageSize?: number
    page?: number
  } = {}): Promise<TwilioIncomingPhoneNumberListResponse> {
    const params = new URLSearchParams()
    params.append('PageSize', String(options.pageSize || 50))
    if (options.page) params.append('Page', String(options.page))
    
    return this.request<TwilioIncomingPhoneNumberListResponse>(
      'GET',
      `/Accounts/${this.accountSid}/IncomingPhoneNumbers.json?${params.toString()}`,
      undefined,
      false
    )
  }
  
  // ==========================================
  // CALLS
  // ==========================================
  
  /**
   * Initiate an outbound call
   */
  async makeCall(
    to: string,
    from: string,
    options: {
      url?: string
      twiml?: string
      statusCallback?: string
      statusCallbackMethod?: string
      statusCallbackEvent?: string[]
      record?: boolean
      recordingStatusCallback?: string
      machineDetection?: 'Enable' | 'DetectMessageEnd'
      machineDetectionTimeout?: number
      timeout?: number
    }
  ): Promise<TwilioCallResponse> {
    const body: Record<string, unknown> = {
      To: to,
      From: from,
    }
    
    if (options.url) body.Url = options.url
    if (options.twiml) body.Twiml = options.twiml
    if (options.statusCallback) body.StatusCallback = options.statusCallback
    if (options.statusCallbackMethod) body.StatusCallbackMethod = options.statusCallbackMethod
    if (options.statusCallbackEvent) {
      options.statusCallbackEvent.forEach((event, i) => {
        body[`StatusCallbackEvent[${i}]`] = event
      })
    }
    if (options.record !== undefined) body.Record = options.record
    if (options.recordingStatusCallback) body.RecordingStatusCallback = options.recordingStatusCallback
    if (options.machineDetection) body.MachineDetection = options.machineDetection
    if (options.machineDetectionTimeout) body.MachineDetectionTimeout = options.machineDetectionTimeout
    if (options.timeout) body.Timeout = options.timeout
    
    return this.request<TwilioCallResponse>(
      'POST',
      `/Accounts/${this.accountSid}/Calls.json`,
      body
    )
  }
  
  /**
   * Get call details
   */
  async getCall(callSid: string): Promise<TwilioCallResponse> {
    return this.request<TwilioCallResponse>(
      'GET',
      `/Accounts/${this.accountSid}/Calls/${callSid}.json`
    )
  }
  
  /**
   * Update/modify a call in progress
   */
  async updateCall(
    callSid: string,
    options: {
      url?: string
      twiml?: string
      status?: 'canceled' | 'completed'
    }
  ): Promise<TwilioCallResponse> {
    return this.request<TwilioCallResponse>(
      'POST',
      `/Accounts/${this.accountSid}/Calls/${callSid}.json`,
      {
        Url: options.url,
        Twiml: options.twiml,
        Status: options.status,
      }
    )
  }
  
  // ==========================================
  // MESSAGING
  // ==========================================
  
  /**
   * Send an SMS/MMS message
   */
  async sendMessage(
    to: string,
    from: string,
    body: string,
    options: {
      mediaUrl?: string[]
      statusCallback?: string
      messagingServiceSid?: string
    } = {}
  ): Promise<TwilioMessageResponse> {
    const requestBody: Record<string, unknown> = {
      To: to,
      From: from,
      Body: body,
    }
    
    if (options.mediaUrl) {
      options.mediaUrl.forEach((url, i) => {
        requestBody[`MediaUrl[${i}]`] = url
      })
    }
    if (options.statusCallback) requestBody.StatusCallback = options.statusCallback
    if (options.messagingServiceSid) requestBody.MessagingServiceSid = options.messagingServiceSid
    
    return this.request<TwilioMessageResponse>(
      'POST',
      `/Accounts/${this.accountSid}/Messages.json`,
      requestBody
    )
  }
  
  // ==========================================
  // USAGE RECORDS
  // ==========================================
  
  /**
   * Get usage records for billing
   */
  async getUsageRecords(options: {
    category?: string
    startDate?: string
    endDate?: string
  } = {}): Promise<TwilioUsageRecordsResponse> {
    const params = new URLSearchParams()
    if (options.category) params.append('Category', options.category)
    if (options.startDate) params.append('StartDate', options.startDate)
    if (options.endDate) params.append('EndDate', options.endDate)
    
    return this.request<TwilioUsageRecordsResponse>(
      'GET',
      `/Accounts/${this.accountSid}/Usage/Records.json?${params.toString()}`,
      undefined,
      false
    )
  }
  
  // ==========================================
  // ACCOUNT VALIDATION
  // ==========================================
  
  /**
   * Validate credentials by fetching account info
   */
  async validateCredentials(): Promise<{ valid: boolean; error?: string }> {
    try {
      await this.request<TwilioSubaccountResponse>(
        'GET',
        `/Accounts/${this.accountSid}.json`
      )
      return { valid: true }
    } catch (error) {
      if (error instanceof TwilioApiError) {
        return { valid: false, error: error.message }
      }
      return { valid: false, error: 'Unknown error' }
    }
  }
}

// ==========================================
// TWILIO API ERROR
// ==========================================

class TwilioApiError extends Error {
  code: number
  status: number
  
  constructor(message: string, code: number, status: number) {
    super(message)
    this.name = 'TwilioApiError'
    this.code = code
    this.status = status
  }
}

// ==========================================
// TWILIO API RESPONSE TYPES
// ==========================================

interface TwilioSubaccountResponse {
  sid: string
  friendly_name: string
  status: 'active' | 'suspended' | 'closed'
  auth_token: string
  date_created: string
  date_updated: string
  type: 'Full' | 'Trial'
  owner_account_sid: string
}

interface TwilioAvailableNumbersResponse {
  available_phone_numbers: Array<{
    phone_number: string
    friendly_name: string
    locality: string
    region: string
    postal_code: string
    iso_country: string
    capabilities: {
      voice: boolean
      sms: boolean
      mms: boolean
      fax: boolean
    }
    beta: boolean
  }>
  uri: string
}

interface TwilioIncomingPhoneNumberResponse {
  sid: string
  account_sid: string
  phone_number: string
  friendly_name: string
  capabilities: {
    voice: boolean
    sms: boolean
    mms: boolean
    fax: boolean
  }
  voice_url: string
  voice_method: string
  voice_fallback_url: string
  sms_url: string
  sms_method: string
  sms_fallback_url: string
  status_callback: string
  status_callback_method: string
  date_created: string
  date_updated: string
}

interface TwilioIncomingPhoneNumberListResponse {
  incoming_phone_numbers: TwilioIncomingPhoneNumberResponse[]
  first_page_uri: string
  next_page_uri?: string
  previous_page_uri?: string
  page: number
  page_size: number
}

interface TwilioCallResponse {
  sid: string
  account_sid: string
  to: string
  from: string
  status: 'queued' | 'ringing' | 'in-progress' | 'completed' | 'busy' | 'no-answer' | 'canceled' | 'failed'
  direction: 'inbound' | 'outbound-api' | 'outbound-dial'
  duration?: string
  price?: string
  price_unit?: string
  answered_by?: 'human' | 'machine_start' | 'machine_end_beep' | 'machine_end_silence' | 'machine_end_other' | 'fax' | 'unknown'
  date_created: string
  date_updated: string
  start_time?: string
  end_time?: string
}

interface TwilioMessageResponse {
  sid: string
  account_sid: string
  to: string
  from: string
  body: string
  status: 'queued' | 'sending' | 'sent' | 'delivered' | 'undelivered' | 'failed'
  direction: 'inbound' | 'outbound-api' | 'outbound-call' | 'outbound-reply'
  num_segments: string
  price?: string
  price_unit?: string
  date_created: string
  date_updated: string
  date_sent?: string
  error_code?: string
  error_message?: string
}

interface TwilioUsageRecordsResponse {
  usage_records: Array<{
    category: string
    description: string
    account_sid: string
    start_date: string
    end_date: string
    count: string
    count_unit: string
    usage: string
    usage_unit: string
    price: string
    price_unit: string
  }>
  first_page_uri: string
  next_page_uri?: string
  previous_page_uri?: string
  page: number
  page_size: number
}

// ==========================================
// TWILIO SERVICE (HIGH-LEVEL OPERATIONS)
// ==========================================

export class TwilioService {
  private masterClient: TwilioApiClient | null = null
  private config: AgencyTwilioConfig | null = null
  
  /**
   * Initialize the service with agency config
   */
  async initialize(config: AgencyTwilioConfig): Promise<void> {
    this.config = config
    this.masterClient = new TwilioApiClient({
      accountSid: config.accountSid,
      authToken: config.authToken,
    })
  }
  
  /**
   * Get a client for a specific subaccount
   */
  getSubaccountClient(twilioAccountSid: string, twilioAuthToken: string): TwilioApiClient {
    return new TwilioApiClient({
      accountSid: twilioAccountSid,
      authToken: twilioAuthToken,
    })
  }
  
  /**
   * Validate master account credentials
   */
  async validateMasterCredentials(accountSid: string, authToken: string): Promise<{ valid: boolean; error?: string }> {
    const client = new TwilioApiClient({ accountSid, authToken })
    return client.validateCredentials()
  }
  
  // ==========================================
  // SUBACCOUNT PROVISIONING
  // ==========================================
  
  /**
   * Provision a new Twilio subaccount for a CRM sub-account
   */
  async provisionSubaccount(
    request: ProvisionSubaccountRequest,
    webhookBaseUrl: string
  ): Promise<ProvisionSubaccountResponse> {
    if (!this.masterClient) {
      return { success: false, error: 'Twilio service not initialized' }
    }
    
    try {
      // Create Twilio subaccount
      const twilioSubaccount = await this.masterClient.createSubaccount(
        request.friendlyName
      )
      
      // Build subaccount record
      const subaccount: Partial<TwilioSubaccount> = {
        subAccountId: request.subAccountId,
        twilioAccountSid: twilioSubaccount.sid,
        twilioAuthToken: twilioSubaccount.auth_token,
        friendlyName: twilioSubaccount.friendly_name,
        status: 'active',
        provisionedAt: new Date().toISOString(),
        canMakeCalls: true,
        canSendSms: false, // Requires 10DLC registration
      }
      
      let phoneNumber: PhoneNumber | undefined
      
      // Auto-provision phone number if requested
      if (request.autoProvisionPhoneNumber && this.config?.autoProvisionPhoneNumber) {
        try {
          const phoneResult = await this.purchaseNumberForSubaccount(
            twilioSubaccount.sid,
            twilioSubaccount.auth_token,
            {
              subAccountId: request.subAccountId,
              agencyId: request.agencyId || '',
              type: request.phoneNumberType || this.config.defaultPhoneNumberType,
              country: this.config.defaultPhoneNumberCountry,
              areaCode: request.areaCode || this.config.defaultPhoneNumberAreaCode || undefined,
              initiatedByUserId: 'system',
            },
            webhookBaseUrl
          )
          
          if (phoneResult.success && phoneResult.phoneNumber) {
            phoneNumber = phoneResult.phoneNumber
          }
        } catch (error) {
          console.error('Failed to auto-provision phone number:', error)
          // Don't fail the whole operation, just log the error
        }
      }
      
      return {
        success: true,
        subaccount: subaccount as TwilioSubaccount,
        phoneNumber,
      }
    } catch (error) {
      console.error('Failed to provision subaccount:', error)
      if (error instanceof TwilioApiError) {
        return { success: false, error: error.message }
      }
      return { success: false, error: 'Failed to provision subaccount' }
    }
  }
  
  /**
   * Suspend a Twilio subaccount
   */
  async suspendSubaccount(
    twilioAccountSid: string,
    reason: string
  ): Promise<{ success: boolean; error?: string }> {
    if (!this.masterClient) {
      return { success: false, error: 'Twilio service not initialized' }
    }
    
    try {
      await this.masterClient.updateSubaccountStatus(twilioAccountSid, 'suspended')
      return { success: true }
    } catch (error) {
      console.error('Failed to suspend subaccount:', error)
      if (error instanceof TwilioApiError) {
        return { success: false, error: error.message }
      }
      return { success: false, error: 'Failed to suspend subaccount' }
    }
  }
  
  /**
   * Reactivate a suspended Twilio subaccount
   */
  async reactivateSubaccount(
    twilioAccountSid: string
  ): Promise<{ success: boolean; error?: string }> {
    if (!this.masterClient) {
      return { success: false, error: 'Twilio service not initialized' }
    }
    
    try {
      await this.masterClient.updateSubaccountStatus(twilioAccountSid, 'active')
      return { success: true }
    } catch (error) {
      console.error('Failed to reactivate subaccount:', error)
      if (error instanceof TwilioApiError) {
        return { success: false, error: error.message }
      }
      return { success: false, error: 'Failed to reactivate subaccount' }
    }
  }
  
  // ==========================================
  // PHONE NUMBER MANAGEMENT
  // ==========================================
  
  /**
   * Search for available phone numbers
   */
  async searchAvailableNumbers(
    country: string,
    type: PhoneNumberType,
    options: {
      areaCode?: string
      contains?: string
      locality?: string
      region?: string
      limit?: number
    } = {}
  ): Promise<AvailablePhoneNumber[]> {
    if (!this.masterClient) {
      throw new Error('Twilio service not initialized')
    }
    
    const response = await this.masterClient.searchAvailableNumbers(
      country,
      type,
      options
    )
    
    return response.available_phone_numbers.map(num => ({
      phoneNumber: num.phone_number,
      friendlyName: num.friendly_name,
      locality: num.locality,
      region: num.region,
      postalCode: num.postal_code,
      country: num.iso_country,
      areaCode: num.phone_number.slice(2, 5), // Extract area code from E.164
      capabilities: {
        voice: num.capabilities.voice,
        sms: num.capabilities.sms,
        mms: num.capabilities.mms,
        fax: num.capabilities.fax,
      },
      monthlyPrice: type === 'toll_free' ? 2.00 : 1.00, // Approximate Twilio pricing
      setupPrice: 0,
    }))
  }
  
  /**
   * Purchase a phone number for a subaccount
   */
  async purchaseNumberForSubaccount(
    twilioAccountSid: string,
    twilioAuthToken: string,
    request: ProvisionPhoneNumberRequest,
    webhookBaseUrl: string
  ): Promise<ProvisionPhoneNumberResponse> {
    const subaccountClient = this.getSubaccountClient(twilioAccountSid, twilioAuthToken)
    
    try {
      // First, search for an available number
      const availableNumbers = await this.searchAvailableNumbers(
        request.country || 'US',
        request.type,
        {
          areaCode: request.areaCode,
          locality: request.locality,
          contains: request.contains,
          limit: 1,
        }
      )
      
      if (availableNumbers.length === 0) {
        return {
          success: false,
          error: 'No phone numbers available matching your criteria',
        }
      }
      
      const selectedNumber = availableNumbers[0]
      
      // Purchase the number under the subaccount
      const purchasedNumber = await subaccountClient.purchaseNumber(
        selectedNumber.phoneNumber,
        {
          friendlyName: request.friendlyName || `RISIVO - ${request.subAccountId}`,
          voiceUrl: `${webhookBaseUrl}/api/v1/webhooks/twilio/voice`,
          smsUrl: `${webhookBaseUrl}/api/v1/webhooks/twilio/sms`,
          statusCallbackUrl: `${webhookBaseUrl}/api/v1/webhooks/twilio/status`,
        }
      )
      
      // Calculate price with markup
      const markup = this.config?.usageMarkupPercent || 5
      const monthlyFee = selectedNumber.monthlyPrice
      const monthlyFeeWithMarkup = monthlyFee * (1 + markup / 100)
      
      const phoneNumber: PhoneNumber = {
        id: '', // Will be set by database
        subAccountId: request.subAccountId,
        twilioSubaccountId: twilioAccountSid,
        phoneNumber: purchasedNumber.phone_number,
        phoneNumberSid: purchasedNumber.sid,
        friendlyName: purchasedNumber.friendly_name,
        type: request.type,
        country: selectedNumber.country,
        region: selectedNumber.region,
        locality: selectedNumber.locality,
        areaCode: selectedNumber.areaCode,
        isoCountry: selectedNumber.country,
        voiceEnabled: purchasedNumber.capabilities.voice,
        smsEnabled: purchasedNumber.capabilities.sms,
        mmsEnabled: purchasedNumber.capabilities.mms,
        faxEnabled: purchasedNumber.capabilities.fax,
        isPrimary: true, // First number is primary
        isDefault: true,
        voiceUrl: purchasedNumber.voice_url,
        voiceMethod: purchasedNumber.voice_method,
        smsUrl: purchasedNumber.sms_url,
        smsMethod: purchasedNumber.sms_method,
        statusCallbackUrl: purchasedNumber.status_callback,
        assignedFor: request.assignedFor || 'all',
        monthlyFee,
        monthlyFeeWithMarkup,
        purchasedAt: new Date().toISOString(),
        purchasePrice: selectedNumber.setupPrice,
        paymentStatus: 'completed',
        status: 'active',
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString(),
      }
      
      return { success: true, phoneNumber }
    } catch (error) {
      console.error('Failed to purchase phone number:', error)
      if (error instanceof TwilioApiError) {
        return { success: false, error: error.message }
      }
      return { success: false, error: 'Failed to purchase phone number' }
    }
  }
  
  /**
   * Release a phone number
   */
  async releaseNumber(
    twilioAccountSid: string,
    twilioAuthToken: string,
    phoneNumberSid: string
  ): Promise<{ success: boolean; error?: string }> {
    const subaccountClient = this.getSubaccountClient(twilioAccountSid, twilioAuthToken)
    
    try {
      await subaccountClient.releaseNumber(phoneNumberSid)
      return { success: true }
    } catch (error) {
      console.error('Failed to release phone number:', error)
      if (error instanceof TwilioApiError) {
        return { success: false, error: error.message }
      }
      return { success: false, error: 'Failed to release phone number' }
    }
  }
  
  // ==========================================
  // CALL OPERATIONS
  // ==========================================
  
  /**
   * Initiate an outbound call
   */
  async makeCall(
    twilioAccountSid: string,
    twilioAuthToken: string,
    to: string,
    from: string,
    webhookBaseUrl: string,
    options: {
      twiml?: string
      url?: string
      record?: boolean
      machineDetection?: boolean
      timeout?: number
    } = {}
  ): Promise<TwilioCallResponse> {
    const client = this.getSubaccountClient(twilioAccountSid, twilioAuthToken)
    
    return client.makeCall(to, from, {
      url: options.url || `${webhookBaseUrl}/api/v1/webhooks/twilio/voice/connect`,
      twiml: options.twiml,
      statusCallback: `${webhookBaseUrl}/api/v1/webhooks/twilio/call-status`,
      statusCallbackMethod: 'POST',
      statusCallbackEvent: ['initiated', 'ringing', 'answered', 'completed'],
      record: options.record ?? true,
      recordingStatusCallback: `${webhookBaseUrl}/api/v1/webhooks/twilio/recording-status`,
      machineDetection: options.machineDetection ? 'DetectMessageEnd' : undefined,
      machineDetectionTimeout: options.machineDetection ? 5 : undefined,
      timeout: options.timeout || 30,
    })
  }
  
  /**
   * End/cancel a call
   */
  async endCall(
    twilioAccountSid: string,
    twilioAuthToken: string,
    callSid: string
  ): Promise<TwilioCallResponse> {
    const client = this.getSubaccountClient(twilioAccountSid, twilioAuthToken)
    return client.updateCall(callSid, { status: 'completed' })
  }
  
  /**
   * Get call details
   */
  async getCallDetails(
    twilioAccountSid: string,
    twilioAuthToken: string,
    callSid: string
  ): Promise<TwilioCallResponse> {
    const client = this.getSubaccountClient(twilioAccountSid, twilioAuthToken)
    return client.getCall(callSid)
  }
  
  // ==========================================
  // MESSAGING OPERATIONS
  // ==========================================
  
  /**
   * Send an SMS message
   */
  async sendSms(
    twilioAccountSid: string,
    twilioAuthToken: string,
    to: string,
    from: string,
    body: string,
    webhookBaseUrl: string,
    options: {
      mediaUrl?: string[]
      messagingServiceSid?: string
    } = {}
  ): Promise<TwilioMessageResponse> {
    const client = this.getSubaccountClient(twilioAccountSid, twilioAuthToken)
    
    return client.sendMessage(to, from, body, {
      mediaUrl: options.mediaUrl,
      statusCallback: `${webhookBaseUrl}/api/v1/webhooks/twilio/message-status`,
      messagingServiceSid: options.messagingServiceSid,
    })
  }
  
  // ==========================================
  // USAGE TRACKING
  // ==========================================
  
  /**
   * Get usage records for a subaccount
   */
  async getUsageRecords(
    twilioAccountSid: string,
    twilioAuthToken: string,
    options: {
      category?: string
      startDate?: string
      endDate?: string
    } = {}
  ): Promise<TwilioUsageRecordsResponse> {
    const client = this.getSubaccountClient(twilioAccountSid, twilioAuthToken)
    return client.getUsageRecords(options)
  }
  
  /**
   * Calculate cost with markup
   */
  calculateCostWithMarkup(
    baseCost: number,
    markupPercent?: number
  ): { baseCost: number; markup: number; total: number } {
    const percent = markupPercent ?? this.config?.usageMarkupPercent ?? 5
    const markup = baseCost * (percent / 100)
    return {
      baseCost,
      markup,
      total: baseCost + markup,
    }
  }
}

// Export singleton instance
export const twilioService = new TwilioService()

// Export types
export { TwilioApiClient, TwilioApiError }
export type {
  TwilioSubaccountResponse,
  TwilioCallResponse,
  TwilioMessageResponse,
  TwilioIncomingPhoneNumberResponse,
  TwilioUsageRecordsResponse,
}
